.calculator {
    min-width    : 300px;
    max-height   : 600px;
    width        : 35vw;
    height       : 80vh;
    background   : rgb(148, 103, 231);
    margin       : 15px;
    border-radius: 5px;
}

.display {
    color        : white;
    font-size    : xx-large;
    font-weight  : bold;
    text-align   : right;
    height       : 20%;
    background   : rgb(72, 33, 117);
    box-shadow   : 0px 5px 8px 0 rgba(0, 0, 0, 0.233);
    margin       : 5px;
    padding      : 5px;
    border-radius: 5px;
    margin-bottom: 10px;
}

.display p {
    margin : 0;
    padding: 0 15px 0 15px;
}

.keysrow {
    height         : 14%;
    margin         : 5px;
    display        : flex;
    flex-direction : column;
    flex-wrap      : wrap;
    justify-content: space-around;
}

.key {
    display: flex;
    justify-content: center;
    align-items: center;
    color       : white;
    font-size   : x-large;
    font-weight : bold;
    text-align  : center;
    height      : 100%;
    width       : 24.58%;
    background  : rgb(72, 33, 117);
    border-color: rgb(148, 103, 231);
}

.key:hover {
    color     : rgb(0, 0, 0);
    background: rgb(148, 103, 231);
    transform : scale(0.95);
    box-shadow   : inset 0px 0px 5px 5px rgba(0, 0, 0, 0.233);
    border-radius: 10px;
}
.btnZero:hover {
    color     : rgb(0, 0, 0);
    background: rgb(255, 255, 255);
    transform : scale(0.95);
    box-shadow   : inset 0px 0px 5px 5px rgba(22, 22, 22, 0.233);
    border-radius: 10px;
}
.btnZero {
    display: flex;
    justify-content: center;
    align-items: center;
    color       : white;
    font-size   : x-large;
    font-weight : bold;
    text-align  : center;
    height      : 100%;
    width       : 49.16%;
    background  : rgb(72, 33, 117);
    border-color: rgb(148, 103, 231);
}